/* Styling logo */
.logo-header img {
    height: auto;
    width: 120px;
    align-items: center;
}

.logo-header {
    width: 750px;
    height: auto;
}

/* Styling header secara keseluruhan */
/* Header styling */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 100px 20px 40px;
    background: linear-gradient(to right, #B4272F, #29226E);
    color: white;
    position: relative;
    z-index: 10;
    width: 100%;
}

/* Styling untuk menu navigasi utama */
/* Navigation links styling */
.nav-links ul {
    list-style: none;
    display: flex;
    grid-gap: 30px;
    gap: 30px;
    margin: 0;
    padding: 0;
    flex-grow: 1;
    justify-content: flex-end;
}

/* Dropdown Container */
.nav-links li {
    position: relative;
}

.nav-links a {
    text-decoration: none;
    color: white;
    font-weight: normal;
    transition: color 0.3s ease;
    white-space: nowrap;
}

/* Active link styling */
.nav-links a.active {
    font-weight: bold;
}

.hamburger-menu {
    display: none;
    background: none;
    border: none;
    font-size: 28px;
    color: white;
    cursor: pointer;
}

.button.active {
    font-weight: bold;
    color: white;
}

.dropdown {
    position: relative;
    cursor: pointer;
}

.dropdown div {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.dropdown-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    color: black;
    list-style: none;
    padding: 10px 0;
    margin: 0;
    margin-top: 10px;
    border-radius: 4px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 10;
    min-width: 220px;
    text-align: left;
}

.dropdown:hover .dropdown-menu {
    display: block;
    border-radius: 4px;
}

.dropdown-menu li {
    margin: 0;
    padding: 0;
    border-radius: 4px;
}

.dropdown-menu li a {
    text-decoration: none;
    color: black;
    font-weight: 500;
    padding: 10px 20px;
    display: block;
    border-radius: 4px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.dropdown-menu li a:hover {
    background-color: #f4f4f4;
    color: #29226E;
    border-radius: 4px;
}

/* Styling the button */
/* Button styling */
.button {
    background-color: #B7262B;
    color: white;
    padding: 12px 25px;
    border-radius: 20px;
    font-size: 16px;
    transition: background-color 0.3s ease, transform 0.3s ease;
    border: none;
    cursor: pointer;
    margin-left: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    white-space: nowrap;
}

/* Hover effect for button */
.button:hover {
    background-color: #9B1E22;
    transform: translateY(-2px);
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

/* Active state for button */
.button:active {
    transform: translateY(0);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}

.language-toggle {
    background-color: transparent;
    border: 1px solid white;
    color: white;
    padding: 5px 10px;
    margin-left: 20px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.language-toggle:hover {
    background-color: #9b1e22;
}

@media (max-width: 800px) {

    .nav-links {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background: rgba(0, 0, 0, 0.438);
        text-align: right;
        padding: 20px;
        opacity: 0;
        transform: translateY(-10px);
        transition: opacity 0.3s ease, transform 0.3s ease;
        pointer-events: none;
        /* Supaya tidak bisa diklik saat hidden */
    }

    .nav-links.mobile-menu-open {
        opacity: 1;
        transform: translateY(0);
        pointer-events: auto;
    }

    .header {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 20px 20px 20px 40px;
    }

    .nav-links ul {
        flex-direction: column;
        grid-gap: 15px;
        gap: 15px;
        margin-left: auto;
        padding-left: 40px;
    }

    .nav-links li {
        display: block;
    }

    /* Tombol hamburger */
    .hamburger-menu {
        order: 1;
        display: block;
        font-size: 28px;
        margin-left: auto;
        padding-left: 20px;
    }

    .button {
        margin-left: 40px;
    }

    .dropdown-menu {
        display: none;
        position: absolute;
        right: 0;
        left: auto;
        background: white;
        padding: 10px 0;
        border-radius: 8px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
        min-width: 250px;
        text-align: right;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }

    .dropdown div {
        justify-content: right;
    }
}

@media (max-width: 480px) {
    .header {
        padding: 10px 20px;
    }

    .logo img {
        width: 80px;
    }

    .hamburger-menu {
        font-size: 24px;
    }

    .button {
        font-size: 12px;
        padding: 8px 16px;
        margin-left: 10px;
    }
}
