.profil-perusahaan-section {
  background-color: #002050;
  color: white;
  padding: 1rem 0 3rem 0;
  display: flex;
  justify-content: center;
}

.profil-content {
  max-width: 1200px;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

/* Profile Image */
.profil-image-container {
  flex: 1 1;
  display: flex;
  justify-content: center;
}

.profil-image {
  margin-left: 40%;
  width: 286px;
  height: 324px;
  border-radius: 8px;
  margin-top: 30px;
  object-fit: cover;
}

/* Profile Text */
.profil-text {
  margin-right: 10%;
  flex: 1 1;
  text-align: left;
  padding: 1% 0 1% 0;
}

.profil-text h2 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  text-align: center;
}

.profil-text p {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 2rem;
  max-width: 100%;
}

/* Profile Contact (Buttons) */
.profile-contact {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 10px;
  justify-content: space-between;
}

.button-icon {
  text-decoration: none;
  background-color: #8fc3f4;
  color: #ffffff;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 15px;
  flex: 1 1;
  grid-gap: 15px;
  gap: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.button-icon:hover {
  transform: scale(1.05);
  opacity: 0.95;
}

.contact-icons {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  color: #ffffff;
  background-color: #8fc3f4;
  flex-shrink: 0;
}

.button-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.button-text h1 {
  margin: 0;
  font-size: 17px;
  font-weight: bold;
  white-space: nowrap;
}

.button-text span {
  font-size: 15px;
  font-weight: normal;
  color: #525C60;
  white-space: nowrap;
}

/* Media Queries */

@media (max-width: 1000px) and (min-width: 800px) {
  .profil-content {
    flex-direction: column;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .profil-image-container {
    flex: none;
    margin-bottom: 1rem;
  }

  .profil-image {
    width: 70%;
    margin-left: 0;
    margin-top: 20px;
  }

  .profil-text {
    text-align: center;
    margin-right: 0;
    width: 90%;
  }

  .profile-contact {
    flex-direction:row;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
  }

  .button-icon {
    max-width: auto;
    padding: 20px 20px;
    margin: 0;
    text-align: center;
    justify-content: center;
  }

  .button-text h1 {
    font-size: 16px;
    white-space: nowrap;  /* Ensures text doesn't wrap */
  }

  .button-text span {
    font-size: 14px;
    white-space: nowrap;  /* Ensures text doesn't wrap */
  }
  .contact-icons {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 800px) {
  .overlay-content h2 {
    font-size: 40px;
