  }
}

@media (max-width: 480px) {
  .overlay-content h2 {
    font-size: 30px;
  }

  .profil-text {
    width: 60%;
    font-size: 10px;
  }

  .profil-text p {
    font-size: 0.9rem;
  }

  /* Button container adjustments */
  .profile-contact {
    flex-direction: column; /* Vertically stack the buttons */
    align-items: center; /* Center buttons horizontally */
    grid-gap: 10px;
    gap: 10px; /* Space between buttons */
    margin-top: 15px;
  }

  .button-icon {
    width: 100%;  /* Make the button width smaller */
    padding: 8px 12px; /* Smaller padding to fit better */
    margin: 0; /* Remove any additional margin */
    text-align: center; /* Ensure the content inside the button is centered */
    display: flex;
    justify-content: center; /* Center the text and icon inside the button */
    align-items: center;
    font-size: 12px; /* Smaller font size inside button */
  }

  .contact-icons {
    margin-right: 8px; /* Add a bit of space between the icon and text */
    width: 30px; /* Make the icon smaller */
    height: 30px; /* Make the icon smaller */
    font-size: 18px; /* Smaller icon size */
  }

  .button-text {
    margin: 0;
    text-align: center; /* Center the text inside the button */
  }

  .button-text h1 {
    font-size: 14px; /* Smaller font size for the title inside the button */
    margin: 0; /* Ensure no extra space around the title */
  }

  .button-text span {
    font-size: 12px; /* Smaller font size for the description inside the button */
    color: #525C60; /* Slightly lighter color for description */
  }
}


