/* bgClear Section */
.bg-clear-container {
  position: relative;
  width: 100%;
  height: auto;
  margin-top: 20px;
}

.bg-clear {
  height: 500px;
  /* Adjusted height for bgClear */
  width: 100%;
  object-fit: cover;
}

.bg-clear-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  grid-gap: 60px;
  gap: 60px;
  /* Larger space between bgDeal and text content */
  align-items: center;
  padding: 20px;
  z-index: 2;
  color: white;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

.overlay-content {
  position: absolute;
  top: 50%;
  left: 50%;
  /* transform: translate(-50%, -50%); */
  text-align: center;
  width: 90%;
  color: white;
  z-index: 2;
}

/* Deal Image (bgDeal) Styling */
.deal-image-wrapper {
  flex: 1 1;
  max-width: 450px;
  height: 100%;
  margin-left: 30px;
}

.bg-deal {
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* Text Content */
.text-content {
  flex: 2 1;
  font-size: 16px;
  line-height: 1.8;
  /* Match line height for better alignment */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 30px; /* Add larger right margin */
  /* max-width: calc(100% - 480px); Dynamically adjust width to match bgDeal */
  margin-right: 30px;
  /* Add larger right margin */
  max-width: calc(100% - 480px);
  /* Dynamically adjust width to match bgDeal */
}

.text-content p {
  margin-bottom: 20px;
  text-align: justify;
  /* Align text for a cleaner look */
}

/* Button Styling */
.button-wrapper {
  text-align: right;
  /* Align button to the right */
  margin-top: 35px;
  /* Move button slightly downward */
}

.contact-button {
  padding: 8px 16px;
  /* Adjust button padding */
  background-color: #b8272c;
  /* Red button */
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 19px;
  text-transform: uppercase;
}

.contact-button:hover {
  background-color: #cc0000;
  /* Darker red on hover */
}

/* Responsive Design */
/* Untuk tampilan tablet (lebar maksimum 800px) */
@media (max-width: 800px) {
  .bg-clear-content {
    flex-direction: row; /* Tata letak horisontal */
    justify-content: space-between; /* Ruang antara gambar dan teks */
    align-items: center; /* Posisikan elemen di tengah secara vertikal */
    grid-gap: 20px;
    gap: 20px; /* Jarak antar elemen */
    padding: 20px; /* Tambahkan padding */
  }

  .deal-image-wrapper {
    flex: 1 1;
    max-width: 300px; /* Lebar gambar diatur agar tidak terlalu besar */
  }

  .bg-deal {
    width: 100%; /* Gambar memenuhi lebar container */
    height: auto; /* Jaga proporsi gambar */
    object-fit: contain; /* Menjaga proporsi gambar */
  }

  .text-content {
    flex: 2 1; /* Memberikan lebih banyak ruang untuk teks */
    font-size: 12px; /* Ukuran font lebih kecil untuk responsif */
    text-align: left; /* Teks rata kiri */
    padding: 0 20px; /* Tambahkan padding pada teks */
    max-width: calc(
      100% - 250px
    ); /* Sesuaikan lebar teks untuk menghindari overflow */
  }

  .text-content p {
    text-align: left; /* Justifikasi teks */
    margin-top: 80px;
  }

  .contact-button {
    font-size: 16px; /* Ukuran tombol sedang */
    padding: 8px 16px; /* Padding tombol */
  }

  .details-section p {
    font-size: 15px;
    margin-left: 12px;
    margin-right: 12px;
  }
}
